* {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	vertical-align: baseline;
	outline: none;
}
$hippie-blue: #4D96A9;
$malibu-blue: #8FE3F9;
$mantree-grey: #87879D;
$alto-grey: #D8D8D8;
$ebony-black: #28283D;
$alabaster-white: #FAFAFA;
$delunge-violet: #855FB1;
$mauve-purple: #D9B8FF;
@font-face {
	font-family: 'Red-Hat-Display';
	src: 
		local('fonts/red-hat-display'), url('../assets/fonts/red-hat-display/red-hat-display-light.ttf') format('ttf'),
		local('fonts/red-hat-display'), url('../assets/fonts/red-hat-display/red-hat-display-light.woff2') format('woff2');
	font-display: block;
	font-weight: 300;
}

@font-face {
	font-family: 'Red-Hat-Display';
	src: 
	local('fonts/red-hat-display'), url('../assets/fonts/red-hat-display/red-hat-display-regular.ttf') format('ttf'),
	local('fonts/red-hat-display'), url('../assets/fonts/red-hat-display/red-hat-display-regular.woff2') format('woff2');
	font-display: block;
	font-weight: 400;
}

@font-face {
	font-family: 'Red-Hat-Display';
	src: 
		local('fonts/red-hat-display'), url('../assets/fonts/red-hat-display/red-hat-display-medium.ttf') format('ttf'),
		local('fonts/red-hat-display'), url('../assets/fonts/red-hat-display/red-hat-display-medium.woff2') format('woff2');
	font-display: block;
	font-weight: 500;
}

@font-face {
	font-family: 'Red-Hat-Display';
	src: 
		local('fonts/red-hat-display'), url('../assets/fonts/red-hat-display/red-hat-display-bold.ttf') format('ttf'),
		local('fonts/red-hat-display'), url('../assets/fonts/red-hat-display/red-hat-display-bold.woff2') format('woff2');
	font-display: block;
	font-weight: 700;
}

@font-face {
	font-family: 'Red-Hat-Display';
	src: 
		local('fonts/red-hat-display'), url('../assets/fonts/red-hat-display/red-hat-display-extrabold.ttf') format('ttf'),
		local('fonts/red-hat-display'), url('../assets/fonts/red-hat-display/red-hat-display-extrabold.woff2') format('woff2');
	font-display: block;
	font-weight: 800;
}

@font-face {
	font-family: 'Red-Hat-Display';
	src: 
		local('fonts/red-hat-display'), url('../assets/fonts/red-hat-display/red-hat-display-black.ttf') format('ttf'),
		local('fonts/red-hat-display'), url('../assets/fonts/red-hat-display/red-hat-display-black.woff2') format('woff2');
	font-display: block;
	font-weight: 900;
}
h1, h2, h3, h4, h5, h6, p, li, td, a, span, button, figure {
  font-family: "Red-Hat-Display";
  font-weight: 300;
}

h1,
.title--h1 {
  font-size: 64px;
  line-height: 64px;
  font-weight: 900;
}

h2,
.title--h2 {
  font-size: 64px;
  line-height: 1;
  font-weight: 900;
}

h3,
.title--h3 {
  font-size: 40px;
  font-weight: 900;
}

h4,
.title--h4 {
  text-transform: uppercase;
  font-weight: 900;
  letter-spacing: 4px;
  font-size: 16px;
}

.text {
  font-size: 18px;
  line-height: 26px;
  color: $mantree-grey;
}

.hero__logo .logo__text {
  line-height: 1;
}

.cta__text {
  font-size: 18px;
  line-height: 26px;
}

.navigator__index {
  font-size: 16px;
  font-weight: 900;
  color: $mantree-grey;
}

.button__version {
  font-weight: 900;
}

@media (max-width: 800px) {
  
	h2, .title--h2 {
    font-size: 48px;
  }

}

@media (max-width: 650px) {
  
	h2, .title--h2 {
    font-size: 40px;
  }
 
	h3, .title--h3 {
    font-size: 32px;
  }
	
}

* {
  box-sizing: border-box;
  scrollbar-width: none;
  scroll-behavior: smooth;
}

*::-webkit-scrollbar {
  display: none;
  -webkit-appearance: none;
}

body, .body {
	width: 100vw;
	overflow-x: hidden;
	transition: all 1s ease;

	&--hidden {
		opacity: 0;
	}

}

.section {
  overflow: hidden;
}

.button {
  padding: 16px 32px;
  color: white;
  border-radius: 32px;
  cursor: pointer;
  font-weight: 900;

  &:not(:last-child) {
    margin-right: 16px;
  }

  &__version {
    margin-left: 4px;
  }

  &--violet {
    background-color: $delunge-violet;

    &:hover {
      background: lighten($delunge-violet, 10%);
    }

    .button__version {
      color: $mauve-purple;
    }

  }

  &--blue {
    background-color: $hippie-blue;

    &:hover {
      background: lighten($hippie-blue, 10%);
    }

    .button__version {
      color: $malibu-blue;
    }

	}

}

.navigator {
  z-index: 5;
  position: relative;

  &__line {
    height: 80px;
    width: 1px;
    background-color: transparentize($mantree-grey, 0.75);
    margin: 0 auto;
  }

  &__index {
    z-index: 5;
    width: 56px;
    height: 56px;
    background-color: $alabaster-white;
    border: 1px solid transparentize($mantree-grey, 0.75);
    border-radius: 50%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
  }

}

.cta {
  margin-top: -22px;
  background: url("/assets//images/cta/wallpaper.png") center center;

  &__text-container {
    margin: 0 auto;
    margin: 120px auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    max-width: 1300px + (2 * 16px);
    padding: 32px;
  }

  &__title {
    width: calc(100% / 3);
    color: $alabaster-white;
    max-width: 350px;
  }

  &__text {
    width: calc(100% / 3);
    max-width: 335px;
    color: $alabaster-white;
  }

  &__button, .button {
    
		&__version {
      color: $mauve-purple;
    }
  
	}

  @media (max-width: 850px) {
 
		&__text-container {
      flex-direction: column;
      margin: 92px 0;
    }

    &__title {
      width: 100%;
      max-width: unset;
      margin-bottom: 32px;
      text-align: center;
    }

    &__text {
      width: 100%;
      max-width: unset;
      margin-bottom: 40px;
      text-align: center;
    }
 
	}

}

.hero {
  &__inner {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }

  &__logo,
  .logo {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 80px auto 104px auto;
    position: relative;

    &__points-container {
      position: relative;
      width: 28px;
      height: 28px;
      margin-right: 8px;
      ;
    }

    &__point,
    .point {
      width: 14px;
      height: 14px;
      border-radius: 50%;
      position: absolute;

      &--violet {
        background-color: $delunge-violet;
        right: 0;
        top: 0;
      }

      &--blue {
        background-color: $hippie-blue;
        left: 0;
        bottom: 0;
      }

    }

    &__text {
      font-size: 24px;
      font-weight: 900;
      text-align: center;
    }

  }

  &__intro,
  .intro {
    margin: 0 auto;
    position: relative;
    display: grid;
    align-items: flex-start;
    grid-template-areas:
      "personas-first text-container personas-last"
      ". navigator .";

    &__text-container {
      grid-area: text-container;
      max-width: 512px;
      flex-shrink: 0;
      padding: 32px;
      margin-bottom: 72px;
    }

    &__title {
      text-align: center;
      margin-bottom: 32px;
    }

    &__text {
      text-align: center;
      margin-bottom: 32px;
    }

    &__buttons {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
    }

    &__personas,
    .personas {
      position: absolute;
      display: grid;
      grid-template-rows: repeat(3, fr);
      grid-template-columns: repeat(3, 1fr);
      flex-wrap: wrap;
      grid-gap: 32px;

      &--first {
        grid-area: personas-first;
        right: 100%;
      }

      &--last {
        grid-area: personas-last;
        left: 100%;
      }

      &__image {
        width: 110px;
        height: 110px;
        border-radius: 50%;
        top: 1000px;

        &:nth-child(2),
        &:nth-child(5) {
          position: relative;
          top: 48px;
        }

      }

    }

    &__navigator {
      grid-area: navigator;
      margin-bottom: 64px;
    }

  }

  @media (max-width: 1150px) {

    &__intro,
    .intro {
      padding-top: 400px;
      grid-template-areas:
        "personas-first personas-last"
        "text-container text-container"
        "navigator navigator";

      &__text-container {
        min-width: 100%;
      }

    }

    &__personas,
    .personas {
      grid-gap: 48px !important;

      &--first {
        right: 0% !important;
        bottom: calc(100% + 48px);
        margin-right: 8px;

        .personas__image:nth-child(2),
        .personas__image:nth-child(5) {
          position: relative;
          top: -48px !important;
        }

      }

      &--last {
        left: 0% !important;
        bottom: calc(100% + 48px) !important;
        margin-left: 8px;

        .personas__image:nth-child(2),
        .personas__image:nth-child(5) {
          top: unset;
        }

        .personas__image:nth-child(1),
        .personas__image:nth-child(3),
        .personas__image:nth-child(4),
        .personas__image:nth-child(6) {
          position: relative;
          top: -48px !important;
        }

      }

      &__image {
        width: 140px !important;
        height: 140px !important;
        border-radius: 50%;
      }

    }

  }

  @media (max-width: 950px) {

    &__intro,
    .intro {
      padding-top: 350px;
    }

    &__personas,
    .personas {
      grid-gap: 48px !important;

      &--first {
        bottom: calc(100% + 48px);

        .personas__image:nth-child(2),
        .personas__image:nth-child(5) {
          position: relative;
          top: -48px !important;
        }

      }

      &--last {
        left: 0% !important;
        bottom: calc(100% + 48px);
        margin-left: 16px;

        .personas__image:nth-child(2),
        .personas__image:nth-child(5) {
          top: unset;
        }

        .personas__image:nth-child(1),
        .personas__image:nth-child(3),
        .personas__image:nth-child(4),
        .personas__image:nth-child(6) {
          position: relative;
          top: -48px !important;
        }

      }

      &__image {
        width: 110px !important;
        height: 110px !important;
        border-radius: 50%;
      }

    }

  }

  @media (max-width: 800px) {

    &__intro,
    .intro {
      padding-top: 300px;
    }

    &__personas,
    .personas {
      grid-gap: 32px !important;

      &--first {
        bottom: calc(100% + 48px);

        .personas__image {

          &:nth-child(2),
          &:nth-child(5) {
            position: relative;
            top: -48px !important;
          }

        }

      }

      &--last {
        left: 0% !important;
        bottom: calc(100% + 48px);
      }

      &__image {
        width: 88px !important;
        height: 88px !important;
        border-radius: 50%;
      }

    }

  }

  @media (max-width: 650px) {

    &__intro,
    .intro {
      padding-top: 300px;
    }

    &__personas,
    .personas {
      grid-gap: 16px !important;

      &--first {
        bottom: calc(100% + 48px);

        .personas__image {

          &:nth-child(2),
          &:nth-child(5) {
            position: relative;
            top: -48px !important;
          }

        }

      }

      &--last {
        left: 0% !important;
        bottom: calc(100% + 48px);
      }

      &__image {
        width: 88px !important;
        height: 88px !important;
        border-radius: 50%;
      }

    }

  }

  @media (max-width: 500px) {

    &__intro,
    .intro {
      padding-top: 300px;

      &__buttons {
        flex-direction: column;
        gap: 16px;
      }

    }

    &__personas,
    .personas {
      grid-gap: 16px !important;

      &--first {
        bottom: calc(100% + 48px);

        .personas__image {

          &:nth-child(2),
          &:nth-child(5) {
            position: relative;
            top: -48px !important;
          }

        }

      }

      &--last {
        left: 0% !important;
        bottom: calc(100% + 48px);
      }

      &__image {
        width: 88px !important;
        height: 88px !important;
        border-radius: 50%;
      }

    }

  }

}
.usecases {
  
	&__images {
    display: grid;
    grid-template-columns: repeat(4, auto);
    margin: 0 auto;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    justify-items: center;
    margin-bottom: 64px;
    grid-gap: 12px;
    padding: 32px;
    max-width: 1300px;
  }

  &__image {
    width: 100%;
    max-width: 100%;
    max-height: 100%;
    border-radius: 8px;
  }

  &__text-container {
    margin: 0 auto;
    max-width: 540px;
    padding: 32px;
    margin-bottom: 72px;
  }

	&__pretitle {
    color: $hippie-blue;
    text-align: center;
    margin-bottom: 16px;
  }

  &__title {
    text-align: center;
    margin-bottom: 32px;
  }

  &__text {
    text-align: center;
  }

  @media (max-width: 850px) {
   
		&__images {
      grid-template-columns: repeat(2, auto);
      grid-gap: 24px;
      margin-bottom: 32px;
		}

	}

  @media (max-width: 650px) {

		&__images {
      grid-template-columns: repeat(2, auto);
      grid-gap: 24px;
    }

    &__text-container {
      margin-bottom: 48px;
    }

	}

  @media (max-width: 500px) {

		&__images {
      grid-template-columns: repeat(2, auto);
      grid-gap: 24px;
    }

    &__text-container {
      margin-bottom: 30px;
    }

	}

}
